import skullIconUrl from "./assets/skull.svg?url";
import blockIconUrl from "./assets/page-block.svg?url";
import warnIconUrl from "./assets/page-warning.svg?url";
import downloadBlockIconUrl from "./assets/download-block.svg?url";
import errorIconUrl from "./assets/error-logo.svg?url";
import sideIlustrationUrl from "./assets/block-page-background.svg?url";

import { getAssetUrl } from "../../utils/assets";
import "./Page.css";
import { BlockPageColor, BlockPageIcon, BlockPageState } from "../../../server/block-page/state";

const bgColorMap: Record<BlockPageColor, string> = {
  red: "rgb(162, 0, 0)",
  gray: "#8c8c8c",
};

const iconsMap: Record<BlockPageIcon, string> = {
  skull: getAssetUrl(skullIconUrl),
  block: getAssetUrl(blockIconUrl),
  warn: getAssetUrl(warnIconUrl),
  downloadBlock: getAssetUrl(downloadBlockIconUrl),
  error: getAssetUrl(errorIconUrl),
};

const illustrationUrl = getAssetUrl(sideIlustrationUrl);

export function Page(props: BlockPageState) {
  return (
    <div style={`--background-color: ${bgColorMap[props.bgColor]};`} class="page-container" data-test-id="malicious-block-page">
     <div class="side-panel">
      <img class="side-panel-illustration" src={illustrationUrl}></img>
     </div>
     <main>
      <img class="icon" src={iconsMap[props.icon]}></img>
      <h1 textContent={props.titleText}></h1>
      <hr/>
      <div innerHTML={props.contentHtml}></div>
     </main>
    </div>
  );
}

