import { ErrorBoundary, render } from "solid-js/web";

import { Page } from "./Page";
import { BlockPageState } from "../../../server/block-page";
import { createEffect } from "solid-js";
import { createStore, reconcile } from "solid-js/store";
import { EmbeddedUiClient } from "../../client";


function onError(error: unknown, reset: () => void) {
  return (<>
    <h1>Something went wrong..</h1>
    error:
    <p>
      {error && error instanceof Error ? error.message : JSON.stringify(error)}
    </p>
    <button onClick={reset}>Try again</button>
  </>);
}

const clientName = (new URLSearchParams(window.location.search)).get("n") ?? "";

const App = () => {
  const [state, setState] = createStore<BlockPageState>({ bgColor: "gray", titleText: "Loading...", contentHtml: "", icon: "warn"});
  createEffect(() => {
    const client = new EmbeddedUiClient<BlockPageState>(clientName);

    client.stateChange.subscribe((state) => {
      setState(reconcile(state));
    });
  });
  return (
    <ErrorBoundary fallback={onError}>
      <Page {...state} />
    </ErrorBoundary>
  );
}


window.addEventListener("load", () => {
  // set the document icon to a generic planet logo
  // the icon is encoded in a data url
  const l = document.createElement("link");
  l.href = "data:image/x-icon;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQEAYAAABPYyMiAAAABmJLR0T///////8JWPfcAAAACXBIWXMAAABIAAAASABGyWs+AAAAF0lEQVRIx2NgGAWjYBSMglEwCkbBSAcACBAAAeaR9cIAAAAASUVORK5CYII=";
  l.rel = "icon";
  l.type = "image/x-icon";
  document.head.appendChild(l);
  const app = document.createElement("div");
  document.body.appendChild(app);
  console.log("EUI: App");
  render(() => <App />, app);
});
