import { Runtime, runtime } from "browser-polyfill";
import { EUIServerMessage } from "../server/protocol/messages";
import { Publisher, Topic } from "@icequb3/events";


export type PageStateContextVal<State> = { loaded: false } | (State & { loaded: true });

type  Port = Runtime.Port;

export type PageStateManagerStatus = "connecting" | "connected" | "disconnected";

export class EmbeddedUiClient<State> {
  private readonly events = {
    stateChange: new Publisher<[State]>({
      handleError(error) {
        console.error("stateChange event listener have thrown", [error])
      }
    }),
    statusChange: new Publisher<[PageStateManagerStatus]>({
      handleError(error) {
        console.error("statusChange event listener have thrown", [error])
      }
    }),
  } as const;

  private status: PageStateManagerStatus = "connecting";
  private readonly port: Port;
  private state?: State;

  public readonly stateChange: Topic<[State]> = this.events.stateChange;
  public readonly statusChange: Topic<[PageStateManagerStatus]> = this.events.statusChange;

  constructor(name: string) {
    this.port = runtime.connect({ name });
    this.port.onMessage.addListener(this.onMessage.bind(this));
    this.port.onDisconnect.addListener(this.onDisconnect.bind(this));
  }

  public getStatus(): PageStateManagerStatus {
    return this.status;
  }

  public getCurrentState(): State {
    if (this.status !== "connected" || this.state === undefined) {
      throw new Error("IllegalStatus: Client is not connected");
    }

    return this.state!;
  }

  private onMessage(message: EUIServerMessage<State>): void {
    switch (message.type) {
      case "hello":
        this.status = "connected";
        this.setState(message.initialState);
        this.events.statusChange.emit("connected");
        break;
      case "state-changed":
        this.setState(message.newState);
    }
  }

  private onDisconnect(): void {
    if (this.port.error) {
      console.error("disconnected with error", this.port.error);
    }

    this.status = "disconnected";
    this.events.statusChange.emit("disconnected");
  }

  private setState(newState: State): void {
    this.state = newState;
    this.events.stateChange.emit(newState);
  }
}

